/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul,
ol {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul,
ol {
  list-style: none;
  list-style-type: none;
}

li {
  list-style-type: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not() {
  text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

body {
  background-image: url("https://www.gannett-cdn.com/-mm-/50cf5bbf732a4dc596a6cd2b25c2d55faffc0cbe/c=0-198-3888-2395/local/-/media/2015/12/10/PAGroup/YorkDailyRecord/635853421689568652-ThinkstockPhotos-461998989.jpg");
  background-size: cover;
  background-attachment: fixed;
}

.App {
  background-color: rgba(0, 0, 0, 0.2);
  overflow-wrap: break-word;
}

.header {
  color: white;
  font-size: 50px;
  border-bottom: 3px solid black;
  box-shadow: 10px 4px 8px 0px rgba(0, 0, 0, 0.2);
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}

.nav__link,
.nav__link:active {
  font-weight: 700;
  width: 100%;
  background-color: #282c34;
  text-decoration: none;
  color: rgb(168, 230, 255);
}

.nav__link:hover {
  background-color: rgb(168, 230, 255);
  color: #282c34;
}

.nav__link-active {
  background-color: rgb(168, 230, 255);
  color: #282c34;
}

.home {
}

.home-title {
  text-align: center;
  margin: 20px 0;
  font-size: 50px;
  color: azure;
}

.films-list {
  margin: 0 auto;
  width: 1700px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: stretch;
}

.film-item {
  width: 400px;
  background-color: rgba(0, 0, 0, 0.918);
  color: azure;
  padding: 20px;
  border-radius: 2px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  box-shadow: 1px 2px 18px 11px rgba(0, 0, 0, 0.301);
}

.film-name,
.film-name:active {
  font-size: 30px;
  margin-top: 20px;
  text-decoration: none;
  color: aliceblue;
}

.movie-details {
  width: 100%;
  min-height: 100vh;
  background-attachment: fixed;
  background-size: cover;
  color: azure;
}

.blur {
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(4px);
  min-height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 30px;
}
.movie-details-left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.2);
  justify-content: flex-start;
}
.movie-details-left > img {
  width: 550px;
  height: 740px;
}
.movie-details-left {
  width: 40%;
  margin-right: 20px;
  padding: 50px 20px;
}
.movie-details-right {
  background: rgba(0, 0, 0, 0.2);

  width: 60%;
  padding: 50px;
}

.genres-list {
  margin-top: 20px;
  display: flex;
}

.genre-item {
  margin-left: 15px;
}

.cast {
  margin-top: 30px;
}

.cast-link,
.cast-link:active {
  font-size: 50px;
  text-decoration: none;
  color: azure;
  background-color: rgba(0, 0, 0, 0.719);
  padding: 10px;
  margin-right: 30px;
}

.cast-list {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}

.cast-item {
  width: 180px;
  margin-bottom: 40px;
}

.reviews-list {
  margin-top: 30px;
}

.review-item {
  margin-bottom: 30px;
}
